import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { HStack, Popover, PopoverBody, PopoverContent, PopoverTrigger, Portal, Text } from '@chakra-ui/react'
import { useNavigate } from 'react-router-dom'
export default function Profile() {
  const { isApp } = useWindowWidth()
  const navigate = useNavigate()
  const { currentAccount, onWalletModal, setConnectCallback } = useAccountStore()

  return (
    <Popover isLazy trigger={isApp ? 'click' : 'hover'} autoFocus={false} returnFocusOnClose={false}>
      <PopoverTrigger>
        <HStack
          gap="0"
          as="button"
          w="36px"
          h="36px"
          borderRadius="8px"
          alignItems="center"
          justify="center"
          border="1px solid"
          borderColor="border"
          cursor="pointer"
          _hover={{
            bg: {
              base: 'block_color_opacity.50',
              lg: 'button_outline_hov_bg'
            },
            svg: {
              fill: 'text_caption'
            }
          }}
          bg="block_color_opacity.50"
          backdropFilter="blur(20px)"
          onClick={(e: any) => {
            cancelBubble(e)
            if (!currentAccount?.address) {
              onWalletModal(true)
              setConnectCallback(() => navigate('/portfolio?tab=wallet'))
            } else {
              navigate('/portfolio?tab=wallet')
            }
          }}
        >
          <Icon xlinkHref="#icon-profile" />
        </HStack>
      </PopoverTrigger>
      {!isApp && (
        <Portal>
          <PopoverContent w="fit-content" maxW={{ base: '300px', lg: 'unset' }}>
            <PopoverBody w="fit-content">
              <Text fontSize="12px">Portfolio</Text>
            </PopoverBody>
          </PopoverContent>
        </Portal>
      )}
    </Popover>
  )
}
